        <el-button class="suggestion-btn" @click="fillExample('outdoor')">
          ËÉΩÂ∏ÆÊàëÂÜô‰∏ÄÁØáÂÖ≥‰∫éÊò•Â§©Êà∑Â§ñÈÉäÊ∏∏ÁöÑÊñáÊ°àÂêóÔºü
        </el-button>
        <el-button class="suggestion-btn" @click="fillExample('skincare')">
          ÊàëÈúÄË¶Å‰∏ÄÊù°ÂÖ≥‰∫éÊó•Â∏∏Êä§ËÇ§ÂàÜ‰∫´ÁöÑÊñáÊ°à„ÄÇ
        </el-button>
        <el-button class="suggestion-btn" @click="fillExample('fitness')">
          ËØ∑‰∏∫ÊàëÂÅö‰∏ÄÊù°ÂÅ•Ë∫´Êó•ËÆ∞ÁöÑÊñáÊ°àÂÜÖÂÆπ„ÄÇ
        </el-button>
      </div>
