<script>
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import { Magic, DocumentCopy, Document, Star, PriceTag, TrendCharts } from '@element-plus/icons-vue'
import axios from 'axios'

export default {
  name: 'App',
  components: {
    Magic,
    DocumentCopy,
    Document,
    Star,
    PriceTag,
    TrendCharts
  },
  setup() {
    const activeTab = ref('note')
    const loading = ref(false)

    const productForm = reactive({
      name: '',
      category: '',
      price: '',
      description: '',
      targetUser: '',
      scenario: ''
    })

    const generatedContent = reactive({
      note: '',
      titles: [],
      tags: [],
      analysis: ''
    })

    // ÊµÅÂºèÁîüÊàêÂÜÖÂÆπ
    const generateContentStream = async () => {
      if (!productForm.name || !productForm.description) {
        ElMessage.warning('ËØ∑Â°´ÂÜô‰∫ßÂìÅÂêçÁß∞ÂíåÊèèËø∞')
        return
      }

      loading.value = true
      
      // Ê∏ÖÁ©∫‰πãÂâçÁöÑÂÜÖÂÆπ
      generatedContent.note = ''
      generatedContent.titles = []
      generatedContent.tags = []
      generatedContent.analysis = ''

      try {
        const response = await fetch('http://localhost:8080/api/notes/generate-stream', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            name: productForm.name,
            description: productForm.description,
            category: productForm.category,
            price: parseFloat(productForm.price.replace(/[^\d.]/g, '')) || 0,
            targetUser: productForm.targetUser,
            scenario: productForm.scenario
          })
        })

        if (!response.ok) {
          throw new Error('ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•')
        }

        const reader = response.body.getReader()
        const decoder = new TextDecoder()
        let buffer = ''

        let done = false
        while (!done) {
          const result = await reader.read()
          done = result.done
          if (done) break
          const value = result.value

          buffer += decoder.decode(value, { stream: true })
          
          // Â§ÑÁêÜÊµÅÂºèÊï∞ÊçÆÔºåÈÄêÂ≠óÊòæÁ§∫
          const lines = buffer.split('\n')
          buffer = lines.pop() // ‰øùÁïôÊúÄÂêé‰∏Ä‰∏™‰∏çÂÆåÊï¥ÁöÑË°å
          
          for (const line of lines) {
            if (line.startsWith('data: ')) {
              const content = line.slice(6)
              if (content.trim()) {
                generatedContent.note += content
              }
            }
          }
        }

        // Â§ÑÁêÜÂâ©‰ΩôÁöÑbuffer
        if (buffer.startsWith('data: ')) {
          const content = buffer.slice(6)
          if (content.trim()) {
            generatedContent.note += content
          }
        }

        ElMessage.success('ÁîüÊàêÂÆåÊàêÔºÅ')

      } catch (error) {
        console.error('ÊµÅÂºèAPIË∞ÉÁî®Â§±Ë¥•:', error)
        ElMessage.error('ÁîüÊàêÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÂêéÁ´ØÊúçÂä°ÊòØÂê¶ÂêØÂä®')
      } finally {
        loading.value = false
      }
    }

    // ‰º†ÁªüÁîüÊàêÂÜÖÂÆπÔºà‰øùÁïô‰Ωú‰∏∫Â§áÈÄâÔºâ
    const generateContent = async () => {
      if (!productForm.name || !productForm.description) {
        ElMessage.warning('ËØ∑Â°´ÂÜô‰∫ßÂìÅÂêçÁß∞ÂíåÊèèËø∞')
        return
      }

      loading.value = true

      try {
        // Ë∞ÉÁî®ÂêéÁ´ØAPI
        const response = await axios.post('http://localhost:8080/api/notes/generate', {
          name: productForm.name,
          description: productForm.description,
          category: productForm.category,
          price: parseFloat(productForm.price.replace(/[^\d.]/g, '')) || 0,
          targetUser: productForm.targetUser,
          scenario: productForm.scenario
        })

        // Êõ¥Êñ∞ÁîüÊàêÁöÑÂÜÖÂÆπ
        const data = response.data
        generatedContent.note = data.note
        generatedContent.titles = data.titles || []
        generatedContent.tags = data.tags || []
        generatedContent.analysis = data.analysis

        ElMessage.success('ÁîüÊàêÊàêÂäüÔºÅ')

      } catch (error) {
        console.error('APIË∞ÉÁî®Â§±Ë¥•:', error)
        ElMessage.error('ÁîüÊàêÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÂêéÁ´ØÊúçÂä°ÊòØÂê¶ÂêØÂä®')
      } finally {
        loading.value = false
      }
    }

    // Â§çÂà∂ÂÜÖÂÆπ
    const copyContent = async (content) => {
      try {
        await navigator.clipboard.writeText(content)
        ElMessage.success('Â§çÂà∂ÊàêÂäü')
      } catch (error) {
        ElMessage.error('Â§çÂà∂Â§±Ë¥•')
      }
    }

    // Â°´ÂÖÖÁ§∫‰æã
    const fillExample = (type) => {
      switch (type) {
        case 'outdoor':
          Object.assign(productForm, {
            name: 'Êò•Â≠£Ë∏èÈùíÂ•óË£Ö',
            category: 'fashion',
            price: '199-299ÂÖÉ',
            description: 'ËΩªËñÑÈÄèÊ∞îÁöÑÊò•Â≠£Â§ñÂ•óÔºåÈÖç‰∏äËàíÈÄÇÁöÑËøêÂä®ÈûãÔºåÂÆåÁæéÁöÑË∏èÈùíË£ÖÂ§á„ÄÇÈù¢ÊñôÊüîËΩØ‰∫≤ËÇ§ÔºåÁâàÂûã‰øÆË∫´ÊòæÁò¶„ÄÇ',
            targetUser: '20-35Â≤ÅÁÉ≠Áà±Êà∑Â§ñÁöÑÂ•≥ÊÄß',
            scenario: 'Êò•Â≠£Ë∏èÈùí„ÄÅÊà∑Â§ñËøêÂä®„ÄÅÊó•Â∏∏‰ºëÈó≤'
          })
          break
        case 'skincare':
          Object.assign(productForm, {
            name: 'ÁéªÂ∞øÈÖ∏‰øùÊπøÁ≤æÂçé',
            category: 'beauty',
            price: '89-129ÂÖÉ',
            description: 'È´òÊµìÂ∫¶ÁéªÂ∞øÈÖ∏ÈÖçÊñπÔºåÊ∑±Â±ÇË°•Ê∞¥‰øùÊπøÔºåË¥®Âú∞Ê∏ÖÁàΩ‰∏çÁ≤òËÖªÔºåÈÄÇÂêàÂêÑÁßçËÇåËÇ§Á±ªÂûã„ÄÇÂùöÊåÅ‰ΩøÁî®ËÇåËÇ§Ê∞¥Ê∂¶ÊúâÂÖâÊ≥Ω„ÄÇ',
            targetUser: '18-40Â≤ÅÊ≥®ÈáçÊä§ËÇ§ÁöÑÂ•≥ÊÄß',
            scenario: 'Êó•Â∏∏Êä§ËÇ§„ÄÅÂπ≤Áá•Â≠£ËäÇ„ÄÅÁÜ¨Â§úÂêé‰øÆÂ§ç'
          })
          break
        case 'fitness':
          Object.assign(productForm, {
            name: 'Áëú‰ºΩÂû´Â•óË£Ö',
            category: 'sports',
            price: '99-159ÂÖÉ',
            description: 'TPEÁéØ‰øùÊùêË¥®ÔºåÈò≤ÊªëËÄêÁ£®ÔºåÂéöÂ∫¶ÈÄÇ‰∏≠Êèê‰æõËâØÂ•ΩÊîØÊíë„ÄÇÈÖçÈÄÅÁëú‰ºΩÂ∏¶ÂíåÊî∂Á∫≥Ë¢ãÔºåÂ±ÖÂÆ∂ÂÅ•Ë∫´ÂøÖÂ§á„ÄÇ',
            targetUser: 'ÂñúÊ¨¢Â±ÖÂÆ∂ÂÅ•Ë∫´ÁöÑ‰∫∫Áæ§',
            scenario: 'Â±ÖÂÆ∂Áëú‰ºΩ„ÄÅÂÅ•Ë∫´ËÆ≠ÁªÉ„ÄÅÊãâ‰º∏ËøêÂä®'
          })
          break
      }
    }

    return {
      activeTab,
      loading,
      productForm,
      generatedContent,
      generateContent,
      generateContentStream,
      copyContent,
      fillExample
    }
  }
}
</script>

